@font-face {
  font-family: 'Banny';
  src:
    url('/assets/fonts/Banny-BoldRound.woff2') format('woff2'),
    url('/assets/fonts/Banny-BoldRound.woff') format('woff'),
    url('/assets/fonts/Banny-BoldRound.ttf') format('truetype');
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

